<template id="title2style">
  <div class="title2">
    <!--                <b-img left alt="Left image" src="static/icon.png" height="50px"></b-img>-->
    <div class="name">
      <span class="title2name" >{{message}}</span>
      <hr v-if="showline" style="border-top: 1.5px ridge rgba(114, 0, 0, 1); margin: 0.3rem 1.5rem 0rem 0;" />
    </div>
  </div>
</template>

<script>
  export default{
    props: [
      'message', 'showline'
    ],
    data () {
      return {
        msg: 'hello vue'
      }
    },
    components: {}
  }
</script>

<style scoped>
  .title2 {
    text-align: left;
    /*background: rgb(122,122,122);*/
    /*background: rgb(12,104,55);*/
    height: 30px;
    margin-bottom: 20px;
    position: relative;
    padding: 0 10px;
  }
  .title2 .name{
    /*background: rgb(12,104,55);*/
    /*background: rgb(122,122,122);*/
    color: rgb(114,0,0);
    position: absolute;
    height: 30px;
    width: 100%;
    margin-bottom: 20px;
    /*margin-left: 31.66px;*/
  }
  .title2name {
    line-height: 30px;
    color:rgb(114,0,0);
    font-weight:700;
    letter-spacing: 1px;
  }
  span{
    font-size: 14px;
  }
</style>



