<template>
  <div  class="main">
    <div  class="intro">
      <b-col cols="3" class="card" v-for="(member, i) in $store.state.members" :key="i">
        <div style="position: center; display: inline-block; width: 5.5rem;" @click="showDetail(i)">
          <b-img-lazy thumbnail @click="showDetail(i)" rounded="0" :alt="member.name+member.position" :src="getImg(member.img)" ></b-img-lazy>
        </div>
        <div style="position: absolute; bottom: 2px; width: 100%; text-align: center;">
          <span style="margin-top: 5px;">{{member.name}}</span>
          <span>{{member.position}}</span>
          <span>Email: {{member.email}}</span>
        </div>

      </b-col>
      <!--          <b-col cols="9">-->
      <!--            <span style="text-indent: 2rem; font-size: 11px; line-height: 18px; letter-spacing: 1px;" v-for="(detail, index) in getDetail(member.introduction)">{{detail}}</span>-->
      <!--          </b-col>-->
      <!--        <hr style="height:3px;border:none;border-top:1px ridge green; margin-top: 20px" />-->
    </div>
  </div>

</template>

<script>
    export default {
        name: "MemberList",
      mainProps: { width: 75, height: 95},
      methods: {
        getImg(img){
          if(img.length === 0){
            return 'static/members/none.jpg'
          }
          return img
        },
        getDetail(detail){
          return detail.split('\n')
        },
        showDetail(index){
          console.log(index)
          this.$router.push({path:'/Group/Member/Detail', query:{index: index}})
        }
      },
      mounted() {
          // document.getElementById('membernav').classList.add("active")
      }
    }
</script>

<style scoped>
  .main{
    width: auto;
    overflow: hidden;
  }
  .card{
    position: relative;
    text-align: center;
    display: inline-block;
    padding: 3px;
    height: 12rem;
    overflow: hidden;
    /*border: none;*/
  }
  .intro {
    min-width: 100px;
    text-align: left;
    font-size: 20px;
  }
  span{
    display: block;
    font-size: 14px;
  }
</style>



