<template>
  <div style="text-align: left; padding-top: 10px;">
      <div style=" margin: 10px 15px 0px 10px; position: center; display: inline-block; width: 8rem; float: left; clear:both;" >
        <b-img-lazy thumbnail rounded="0" :alt="member.name+member.position" :src="getImg(member.img)" ></b-img-lazy>
      </div>
    <div style="position: relative;">
      <p v-for="(detail, i) in getDetail(member.introduction)" :key="i">{{detail}}</p>
    </div>
  <!--  <span>{{getDetail($store.state.members[index].introduction)}}</span>-->
  </div>
</template>

<script>
    export default {
        name: "MemberDetail",

      data(){
          return {
            // mainProps: { width: 75, height: 95},
            index: 0,
            member: {}
          }
      },
      methods:{
        getImg(img){
          if(img.length === 0){
            return 'static/members/none.jpg'
          }
          return img
        },
        getDetail(detail){
          return detail.split('\n')
        },
      },
      created() {
        this.index = this.$route. query.index
        this.member = this.$store.state.members[this.index]
      },
      mounted() {
        this.index = this.$route. query.index
        this.member = this.$store.state.members[this.index]
      }
    }
</script>

<style scoped>
  p{
    text-indent: 2rem;
    font-size: 14px;
    margin: 0;
    /*display: inline-block;*/
    /*line-height: 18px; letter-spacing: 1px;*/
  }
  .main{
    padding: 20px;
    width: auto;
    overflow: hidden;
  }
  .card{
    position: absolute;
    text-align: center;
    display: inline-block;
    padding: 3px;
    /*height: 12rem;*/
    overflow: hidden;
    /*border: none;*/
  }
  .intro {
    min-width: 100px;
    text-align: left;
    font-size: 20px;
  }
</style>



