<template>
  <div>
    <div class="main">
      <title2 :message="getTitle()" showline="false"></title2>
      <router-view></router-view>

    </div>
  </div>
</template>

<script>
  import title2 from '../components/Title2'

  export default {
    components:{
      title2
    },
      name: "Member",
      data(){
          return {
            // mainProps: { width: 75, height: 95},
            items:[
              {
                text: '成员列表',
                href: '/Group'
              },
              {
                text: '成员简介',
                href: ''
              }
            ]
          }
      },
      methods: {
        getImg(img){
          if(img.length === 0){
            return 'static/members/none.jpg'
          }
          return img
        },
        getDetail(detail){
          return detail.split('\n')
        },
        showDetail(index){
          this.$router.push({path:'/Group/Member/Detail'})
        },
        getTitle(){
          let rou = this.$route.path.split('/')
          if(rou[rou.length-1] === 'List'){
            return '主要成员列表'
          }
          return '成员简介'
        }
      },
      mounted() {
        this.$router.push({path:'/Group/Member/List'}).catch(err => {err})
      }
    }
</script>

<style scoped>
  .main{
    width: auto;
    overflow: hidden;
  }
  /*.title2 {*/
  /*  text-align: left;*/
  /*  background: rgb(12,104,55);*/
  /*  height: 30px;*/
