  /*  height: 20px;*/
  /*  width: auto;*/
  /*  margin-left: 19px;*/
  /*  padding-left: 20px;*/
  /*}*/
  /*.title2name {*/
  /*  line-height: 30px;*/
  /*  color: #fff;*/
  /*  font-weight:700;*/
  /*  letter-spacing: 1px;*/
  /*  font-size: 14px;*/
  /*}*/
  .intro {
    min-width: 100px;
    text-align: left;
    font-size: 20px;
  }
  span{
    display: block;
    font-size: 14px;
  }
  .card{
    text-align: center;
    display: inline-block;
    padding: 3px;
    height: 11.5rem;
    /*border: none;*/
  }
</style>



